@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;400;500;700&display=swap);
/* Google fonts*/

*{
    scroll-behavior: smooth;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Poppins, sans-serif;
    cursor: default;
    }

.App{
    margin-top: 8vh;
    overflow: hidden;
}

.navbar{
    background-color: white;
}

.footer{
    background-color:#3b4252;
    color: white;
}

a{
    cursor: pointer;
}

.hover-underline-animation {
  display: inline-block;
  position: relative;
}

.hover-underline-animation:after {
  content: '';
  position: absolute;
  width: 100%;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #0087ca;
  -webkit-transform-origin: bottom left;
          transform-origin: bottom left;
  transition: -webkit-transform 0.25s ease-out;
  transition: transform 0.25s ease-out;
  transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
}

.hover-underline-animation:hover:after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transform-origin: bottom left;
          transform-origin: bottom left;
}

.white-line:after{
    background-color:#f2f2f2;
}


.Italent{
  display: flex;
  width: 60%;
  margin-left: 20%;
  margin-bottom: 15vh;
}

.Italent .content, .action{
  width: 50%;
}

.Italent .content{
  background-color: #3b4252;
  color: #f2f2f2;
  padding: 2%;
}

.Italent .action{
  background: #0087ca;
  color: #f2f2f2;
  padding: 2%;
}

.Italent .action a { 
  text-decoration: none;
  color: #f2f2f2 !important;
}
.btn{
    display: inline-block;
    padding: 1.5% 2.5%;
    text-decoration: none;
    font-weight: 500;
    cursor: pointer;
}

.btn-primary{
    color: white !important;
    background-color: #0087ca;
}

.btn-secondary{
    color: #0087ca;
    background-color: rgba(0, 0, 0, 0.05);
}

.navbar{
    /* Size */
    width: 100vw;
    height: 8vh;
    /* Margins & Padding */
    padding: 0 2vw 0 2vw;
    /* Display */
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* Style */
    border-bottom: solid 1px rgba(0, 0, 0, 0.05);
    top: 0;
    position: fixed;
    z-index: 2;
    font-size: 16px;
}

.navbar div{
    /* Size */
    width: 100%;
    height: 100%;
    /* Display */
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.navbar .items{
    /* Display */
    justify-content: space-around;
}

.navbar .items a{
    /* Margins & Padding */
    /* padding: 1% 2%;
    /* Style */
    text-decoration: none;
    transition: all 0.5s ease-in-out;
    color: black;
}

.navbar .items a:hover{
    /* Style */
    /*background-color: rgba(0, 0, 0, 0.05);*/ 
    cursor: pointer;
}



.blog{
    background-color: #0087ca;
    color: white;
    display: flex;
    flex-direction: column;
    padding: 0 5%;
    align-items: start;
    justify-content: center;
    width: 100%;
    height: 12vh;
}


.blog a{
    font-weight: 700;
    cursor: pointer;
    text-decoration:none;
    color: white;
    cursor: pointer;
    display: block;
}

.blog .hover-underline-animation {
  display: inline-block;
  position: relative;
}

.blog .hover-underline-animation:after {
  content: '';
  position: absolute;
  width: 100%;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: white;
  -webkit-transform-origin: bottom left;
          transform-origin: bottom left;
  transition: -webkit-transform 0.25s ease-out;
  transition: transform 0.25s ease-out;
  transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
}

.blog .hover-underline-animation:hover:after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transform-origin: bottom left;
          transform-origin: bottom left;
}

